@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
a{
    color:black !important
}
.brand-logo{
    font-family: 'Grand Hotel', cursive;
}
.my-card{
    margin-top: 50px;
}
.auth-card{
    padding: 20px;
    text-align: center;
    max-width: 400px;
    margin: 10px auto;
}
.login-heading{
    font-family: 'Grand Hotel', cursive;
}
.input-field input[type=text]:focus {
    border-bottom: 1px solid #1a7eac !important;
    box-shadow: 0 1px 0 0 #1a7eac !important;
  }
  .input-field input[type=password]:focus {
    border-bottom: 1px solid #1a7eac !important;
    box-shadow: 0 1px 0 0 #1a7eac !important;
  }
  .gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .item{
    width: 30%;
  }
  
.home-card{
    max-width: 500px;
    height:-webkit-max-content;
    height:-moz-max-content;
    height:max-content;
    margin:26px auto;
}
.collection-item{
    width: 100%;
  }
  
.file-field input[type=text]:focus {
    border-bottom: 1px solid #1a7eac !important;
    box-shadow: 0 1px 0 0 #1a7eac !important;
  }
#toast-container{
    top:30px !important;
    right:20px;
    left:auto !important;

}

